import pageHeader from './base/pageHeader.vue'
import pageHot from './base/pageHot.vue'
import interfaces from '../../utils/interface.js'




export default {
	data() {
		return {
			// 轮播图片
			swiperList: [],
			// 分类菜单
			categoryList: [],
			currentSwiper: 0, //当前轮播图显示下标
			//热卖商品
			hotList: [],
			showTitle: true
		}
	},
	components: {
		pageHeader,
		pageHot
	},
	onLoad(option) {

		this.initData();

		// wx.showShareMenu的真正作用，是控制是否显示转发分享菜单在 "...”菜单中，而不是直接弹出分享界面。
		
		// wx.showShareMenu 的参数 withShareTicket 为 true 时，点击模拟器右上角菜单后出现的转发按钮，会出现一个测试群列表

		uni.showShareMenu({ //
			withShareTicket : true,
		})

	},
	methods: {
		async initData() {
			 
			this.request({
				url: interfaces.get_banner, //获取轮播图
				method: "POST",
				success: ((res) => {
					this.swiperList = res.data
				})
			});
			this.request({
				url: interfaces.get_hotgoods, //获取热卖商品
				method: "POST",
				success: ((res) => {
					this.hotList = res.data;
				})
			});
			this.request({
				url: interfaces.get_bigclass, //获取分类名称
				success: ((res) => {
					this.categoryList = res.data;
				})
			});
			









		},
		
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		//分类跳转
		toCategory(e) {
			uni.navigateTo({
				url: "../category/toCategory?goods_id=" + e
			})
		},
		toCart() {
			uni.switchTab({
				url: "../cart/cart"
			})
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh();
			this.initData();
		}, 1000);
	},
}
